let url = ''
let token = ''
let limit = 1000
const dataApi = {
    requestData(from, to) {
        if (!url) return
        let format = 'yyyy-mm-dd HH:MM:00'
        return http.post(url, {
            start_time: dateFormat(from, format),
            end_time: dateFormat(to, format),
            token,
            limit: limit + ''
        })
    },
    mockRequest(from, to) {
        // let data = randomData(from, to)
        // fromtoevent
        let newData = {}
        for (let time in eventData) {
            if (time * 1 >= from && time * 1 < to) {
                newData[time] = {...eventData[time]}
            }
        }

        //0.5
        return new Promise(resolve => {
            setTimeout(() => {
                resolve(newData)
            }, 500)
        })
    },
    setRequestUrl(newUrl) {
        url = newUrl
        // http.defaults.baseURL = newUrl
    },
    setToken(newToken) {
        token = newToken
    },
    setLimit(newLimit) {
        limit = newLimit
    }
}
export default dataApi
